/* eslint-disable no-useless-constructor */
import React from 'react';
import request from "../api/api"
import Cookie from "js-cookie"
import PubSub from 'pubsub-js'
class A extends React.Component {
    constructor(props) {
        super(props)
        this.onblur3 = this.onblur3.bind(this)
    }
    onblur3(e) {
        var value = e.target.value;
        PubSub.publish("value", value)

    }
    render() {
        return (
            <fieldset className="form-group" >
                <input className="form-control form-control-lg"
                    type="text"
                    placeholder="Your Name"
                    onBlur={this.onblur3}
                />
            </fieldset>

        )
    }
}
class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            email: "123",
            password: "123"
        }
        this.onsubmit = this.onsubmit.bind(this)
        this.onblur1 = this.onblur1.bind(this)
        this.onblur2 = this.onblur2.bind(this)
    }

    componentDidMount() {
        PubSub.subscribe("value", (msg, data) => {
            this.setState({
                username: data
            })
        })
    }
    isLogin() {
        //判断是注册还是登陆  如果是register 就是注冊(Sign up)  如果是login 就是登錄(Sign in)
        return this.props.match.path === "/login"
    }

    onsubmit(event) {
        //阻止表单提交
        event.preventDefault();
        let user = {
            username: this.state.username ? this.state.username : "",
            email: this.state.email,
            password: this.state.password
        }
        request({
            method: "POST",
            url: this.state.username ? "/api/users" : "/api/users/login",
            data: {
                user: user
            }
        }).then(res => {
            console.log(res);
            //实现跨组件跳转
            this.props.history.push("/");
            //把token值保存到Cookie里
            Cookie.set('auth', res.data.user.token);
            Cookie.set('user', res.data.user.username);
            //发布模式
            PubSub.publish('auth', res.data.user.token);
            PubSub.publish('user', res.data.user.username)
        })
    }
    onblur1(e) {
        this.setState({
            email: e.target.value
        })
    }
    onblur2(e) {
        this.setState({
            password: e.target.value
        })
    }
    render() {
        // console.log(this.state)
        return (<div >
            <div className="auth-page" >
                <div className="container page" >
                    <div className="row" >

                        <div className="col-md-6 offset-md-3 col-xs-12" >
                            <h1 className="text-xs-center" > {this.isLogin() ? "Sign in" : "Sign up"} </h1>
                            <p className="text-xs-center" >
                                <a href="" > {this.isLogin() ? "Need an account?" : "Have an account?"} </a> </p> <ul className="error-messages" >
                                <li > That email is already taken </li> </ul>

                            <form onSubmit={this.onsubmit} > {this.isLogin() ? "" : < A />}
                                <fieldset className="form-group" ><input className="form-control form-control-lg"
                                    type="text"
                                    placeholder="Email"
                                    onBlur={this.onblur1}
                                /> </fieldset> <fieldset className="form-group" >
                                    <input className="form-control form-control-lg"
                                        type="password"
                                        placeholder="Password"
                                        onBlur={this.onblur2} />
                                </fieldset>
                                <button className="btn btn-lg btn-primary pull-xs-right" > {this.isLogin() ? "Sign in" : "Sign up"} </button>
                            </form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        );
    }
}

export default Login;