import React from 'react';
import Header from "./Header/index"
import Fooder from "./Fooder/index"
import Home from "./Home/index"
import Article from "./Article/index"
import Login from "./Login/index"
import Settings from "./Settings/index"
import Profile from "./Profile/index"
import Editor from "./Editor/index"
import Cookie from "js-cookie"
import PubSub from 'pubsub-js'
import { BrowserRouter as Router, Route, Switch, } from "react-router-dom";
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      auth: ""
    }
  }
  componentDidMount() {
    PubSub.subscribe("auth", (msg, data) => {
      // console.log(msg, data)
      this.setState({
        auth: data
      })
    })
  }
  render() {
    if (Cookie.get("auth")) {
      return (
        <div>
          <Router>
            <Header />
            <Switch>
              <Route exact path="/" component={Home}></Route>
              <Route exact path="/editor" component={Editor}></Route>
              <Route exact path="/register" component={Home}></Route>
              <Route exact path="/login" component={Home}></Route>
              <Route exact path="/settings" component={Settings}></Route>
              <Route exact path="/profile" component={Profile}></Route>
              <Route exact path="/article" component={Article}></Route>
            </Switch>
            <Fooder />
          </Router>
        </div>
      )
    } else {
      return (
        <div>
          <Router>
            <Header />
            <Switch>
              <Route exact path="/" component={Home}></Route>
              <Route exact path="/editor" component={Home}></Route>
              <Route exact path="/register" component={Login}></Route>
              <Route exact path="/login" component={Login}></Route>
              <Route exact path="/settings" component={Home}></Route>
              <Route exact path="/profile" component={Home}></Route>
              <Route exact path="/article" component={Article}></Route>
            </Switch>
            <Fooder />
          </Router>
        </div>
      )
    }
  }
}
export default App