/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { NavLink } from 'react-router-dom';
import request from "../api/api"
import { addFavorite, deleFavorite } from "../api/favorite"
class home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            articles: [],
            list: [],
            totalPage: [],
            tage: 0,
            tags: [],
            //数据在加载过程中点赞静止
            favoritedisabled: false
        }
        this.pageClick = this.pageClick.bind(this)
        this.fnFavorite = this.fnFavorite.bind(this)
    }

    //分页的跳转
    pageClick = (i) => {
        request({
            method: "get",
            url: "/api/articles",
            params: {
                limit: 20,
                offset: i
            }
        }).then((res) => {
            this.setState({
                articles: res.data.articles,
                tage: i
            })
        })
    }

    //点赞
    async fnFavorite(data) {

        //在数据请求的过程中不允许点击
        await this.setState({          //异步
            favoritedisabled: true
        })

        console.log(data);//当前点击的单个文章
        if (data.favorited) {
            //取消点赞业务
            await deleFavorite(data).then((res) => {  //异步
                console.log("取消", res)
            })
            data.favorited = false
            data.favoritesCount += -1

            let key = this.state.articles.indexOf(data);
            this.state.articles[key] = data
            await this.setState({                            //异步
                articles: this.state.articles
            })
        } else {
            //点赞业务
            await addFavorite(data).then((res) => {   //异步
                console.log("添加", res)
            })
            data.favorited = true
            data.favoritesCount += 1
            // console.log(this.state.articles.indexOf(data))
            let key = this.state.articles.indexOf(data);
            this.state.articles[key] = data
            await this.setState({                          //异步
                articles: this.state.articles
            })
        }

        //请求结束使点击按钮回复使用
        await this.setState({                         //异步
            favoritedisabled: false
        })


    }

    componentDidMount() {

        request({
            method: "GET",
            url: "/api/articles"
        }).then(res => {
            // console.log(res)
            this.setState({
                articles: res.data.articles,
                totalPage: res.data.articlesCount / res.data.articles.length
            })
            var list = []
            for (var i = 0; i < this.state.totalPage; i++) {
                list.push(i)
            }
            // console.log(list)
            this.setState({
                list: list
            })
        })
        request({
            method: "GET",
            url: "/api/tags"
        }).then(ok => {
            this.setState({
                tags: ok.data.tags
            })
        })
    }

    render() {
        return (
            <div className="home-page">

                <div className="banner">
                    <div className="container">
                        <h1 className="logo-font">conduit</h1>
                        <p>A place to share your knowledge.</p>
                    </div>
                </div>

                <div className="container page">
                    <div className="row">

                        <div className="col-md-9">
                            <div className="feed-toggle">
                                <ul className="nav nav-pills outline-active">
                                    <li className="nav-item">
                                        <a className="nav-link disabled" href="">Your Feed</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link active" href="">Global Feed</a>
                                    </li>
                                </ul>
                            </div>

                            {this.state.articles.map((data, i) => {
                                return <div className="article-preview" key={i}>
                                    <div className="article-meta">
                                        <a href="profile.html"><img src={data.author.image} /></a>
                                        <div className="info">
                                            <a href="" className="author">{data.author.username}</a>
                                            <span className="date">{data.createdAt}</span>
                                        </div>
                                        <button onClick={() => { this.fnFavorite(data) }} disabled={this.state.favoritedisabled} className="btn btn-outline-primary btn-sm pull-xs-right">
                                            <i className="ion-heart"></i> {data.favoritesCount}
                                        </button>
                                    </div>
                                    <a href="" className="preview-link">
                                        <h1>{data.title}</h1>
                                        <p>{data.description}</p>
                                        <span>Read more...</span>
                                    </a>
                                </div>
                            })}
                            <nav>
                                <ul className="pagination">
                                    {
                                        this.state.list.map((data, index) => {
                                            return (<li key={index} className={this.state.tage === index ? "page-item ng-scope active" : "page-item ng-scope"} ng-class="{active: pageNumber === $ctrl.currentPage }" ng-repeat="pageNumber in $ctrl.pageRange($ctrl.totalPages)" ng-click="$ctrl.changePage(pageNumber)">
                                                <NavLink className="page-link ng-binding" to="/" onClick={() => this.pageClick(index)} >{index + 1}</NavLink>
                                            </li>)
                                        })
                                    }
                                </ul>
                            </nav>
                        </div>

                        <div className="col-md-3">
                            <div className="sidebar">
                                <p>Popular Tags</p>
                                <div className="tag-list">
                                    {this.state.tags.map((data, i) => {
                                        return <a href="" className="tag-pill tag-default" key={i}>{data}</a>
                                    })}

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default home;