import React, { Component } from 'react';
import "../min.css"
import { NavLink, Link } from "react-router-dom";
import Cookie from "js-cookie"
import PubSub from 'pubsub-js'
class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            auth: "",
            username: ""
        }
    }

    componentDidMount() {
        if (Cookie.get("user")) {
            this.setState({
                username: Cookie.get("user")
            })
        }
        PubSub.subscribe("auth", (msg, data) => {
            // console.log(msg, data)
            this.setState({
                auth: data
            })
        })
        PubSub.subscribe("user", (msg, data) => {
            this.setState({
                username: data
            })
        })
    }

    render() {
        if (Cookie.get("auth")) {
            return (
                <div>
                    <nav className="navbar navbar-light">
                        <div className="container">
                            <a className="navbar-brand" href="index.html">conduit</a>
                            <ul className="nav navbar-nav pull-xs-right">
                                <li className="nav-item">
                                    <NavLink className="nav-link active" to="/" activeClassName="active">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/editor">
                                        <i className="ion-compose"></i>&nbsp;New Article
                                    </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/settings" >
                                        <i className="ion-gear-a"></i>&nbsp;Settings
                                    </NavLink>
                                </li>

                                <li className="nav-item">
                                    <NavLink className="nav-link ng-binding active" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })" to="/profile">
                                        <img ng-src="http://www.baidu.com/img/bdlogo.png" className="user-pic" src="http://www.baidu.com/img/bdlogo.png" />
                                        {this.state.username}
                                    </NavLink>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            );
        } else {
            return (
                <div>
                    <nav className="navbar navbar-light">
                        <div className="container">
                            <a className="navbar-brand" href="index.html">conduit</a>
                            <ul className="nav navbar-nav pull-xs-right">
                                <li className="nav-item">
                                    <NavLink className="nav-link active" to="/" activeClassName="active">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/login">Sign in</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/register">Sign up</NavLink>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            );
        }
    }
}

export default Header;